#root {
  display: flex;
  justify-content: center;
  align-items: center;
}

.about__box {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: white;
  width: 55%;
  padding: 30px 50px;
  border-radius: 10px;
  height: 50%;
}

.about__box p {
  font-family: "Nanum Gothic Coding", monospace;
}

.about__box .detail_desc {
  line-height: 140%;
}

@media screen and (max-width: 1240px) {
  .about__box {
    width: 60%;
  }

  .about__box p {
    font-size: 14px;
  }
}

/* Tablet */
@media screen and (max-width: 1024px) {
  .about__box {
    width: 70%;
    height: 40%;
  }
}

@media screen and (max-width: 768px) {
  .about__box {
    width: 80%;
    height: 30%;
  }
}

@media screen and (max-width: 600px) {
  .about__box p {
    font-size: 13px;
  }
}

/* mobile */
@media screen and (max-width: 480px) {
  .about__box  {
    width: 85%;
    padding: 0 20px;
  }

  .about__box p {
    font-size: 11px;
  }
}

@media screen and (max-width: 380px) {
  .about__box  {
    width: 85%;
    height: 40%;
  }

  .about__box p {
    font-size: 11px;
  }
}
.movies {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
  padding: 0;
  padding-top: 80px;
  width: 100%;
}

.movies a {
  width: 45%;
}

.movies .movie {
  background-color: var(--white-color);
  margin-bottom: 70px;
  margin-right: 10px;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  padding: 20px;
  border-radius: 6px;
  color: #adaeb9;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.movies .movie img {
  position: relative;
  top: -40px;
  max-width: 120px;
  margin-right: 20px;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.movies .movie .movie__data {
  padding-right: 20px;
  margin-top: 12px;
  display: inline-block;
}

.movies .movie .movie__data .movie__title,
.movies .movie .movie__data .movie__year {
  margin: 0;
}

.movies .movie .movie__data .movie__title {
  margin-bottom: 8px;
  font-weight: 400;
  font-size: 18px;
  color: #2c2c2c;
}

.movies .movie .movie__data .movie__genres {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  margin-top: 5px;
}

.movies .movie .movie__data .movie__genres li {
  margin-right: 8px;
}

.movies .movie .movie__data .movie__summary {
  font-size: 14px;
}

@media screen and (max-width: 1380px) {
  .movies .movie img {
    max-width: 110px;
  }
  .movies .movie .movie__data .movie__title {
    font-size: 18px;
  }

  .movies .movie .movie__data .movie__genres {
    font-size: 14px;
  }

  .movies .movie .movie__data .movie__summary {
    font-size: 13px;
  }
}

@media screen and (max-width: 1200px) {
  .movies {
    width: 95%;
  }
}

@media screen and (max-width: 1180px) {
  .movies {
    width: 100%;
  }

  .movies .movie {
    margin-bottom: 40px;
  }
}

@media screen and (max-width: 1100px) {
  .movies .movie img {
    max-width: 100px;
  }

  .movies .movie .movie__data .movie__title {
    font-size: 16px;
  }

  .movies .movie .movie__data .movie__genres {
    font-size: 13px;
  }
}

/* Tablet */
@media screen and (max-width: 1024px) {
  .movies {
    display: block;
    width: 95%;
    margin-top: 40px;
    margin-left: ;
  }
  .movies .movie {
    margin-bottom: 60px;
  }

  .movies .movie img {
    max-width: 140px;
  }

  .movies .movie .movie__data {
    margin-left: 30px;
  }

  .movies .movie .movie__data .movie__title {
    font-size: 24px;
    margin-bottom: 20px;
  }

  .movies .movie .movie__data .movie__genres {
    font-size: 16px;
  }

  .movies .movie .movie__data .movie__summary {
    font-size: 14px;
  }
}

/* mobile */
@media screen and (max-width: 480px) {
  .movies {
    display: block;
    width: 80%;
  }

  .movies a {
    width: 40%;
  }

  .movies .movie {
    padding: 15px;
    margin-right: 0;
    padding-right: 70px;
  }

  .movies .movie img {
    max-width: 100px;
  }

  .movies .movie .movie__data .movie__title {
    font-size: 15px;
  }
}

:root {
  /* Color */
  --white-color: #fff;
  --black-color: #000;
  --light-mint-color: #c7e9e1;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Noto Sans";
  background-color: #c7e9e1;
  background-color: var(--light-mint-color);
}

html,
body,
#root,
.container {
  height: 100%;
  display: flex;
  justify-content: center;
}

.container .loader {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 300;
  font-size: 22px;
}

@media screen and (max-width: 1024px) {
  .container {
    margin-left: 18px;
  }
}

/* mobile */
@media screen and (max-width: 480px) {
  .container {
    padding-top: 60px;
    margin-left: 80px;
  }

  .container .loader {
    font-size: 18px;
  }
}
#root {
  width: 80%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.movie__detail {
  width: 85%;
  font-size: 20px;
  display: flex;
  font-family: 'Noto Sans', 'Courier New', Courier, monospace;
}

.movie__detail img {
  width: 500px;
  height: 550px;
  margin-right: 70px;
  box-shadow: 10px 8px 10px 10px rgba(0, 0, 0, 0.24);
}

.movie__detail .movie__detail__data {
  margin-top: 40px;
  padding-right: 20px;
}

.movie__detail .movie__detail__data .movie__title {
  font-size: 28px;
  margin-bottom: 30px;
}

.movie__detail .movie__detail__data .movie__year {
  font-size: 18px;
}

.movie__detail .movie__detail__data .movie__genres {
  list-style: none;
  padding: 0;
  display: flex;
}

.movie__detail .movie__detail__data .movie__genres .movie__genre {
  margin-right: 10px;
}

.movie__detail .movie__detail__data .movie__summary {
  margin-top: 60px;
  font-size: 18px;
  color: #2c2c2c;
}

.movie__detail h2,
.movie__detail h4 {
  margin: 0;
}

/* Tablet */
@media screen and (max-width: 1024px) {
  .movie__detail {
    width: 95%;
    height: 80%;
    justify-content: center;
    align-items: center;
  }

  .movie__detail img {
    width: 290px;
    height: 330px;
    margin-right: 70px;
  }

  .movie__detail .movie__detail__data .movie__summary {
    margin-top: 30px;
    font-size: 15px;
  }
}

/* mobile */
@media screen and (max-width: 480px) {
  #root {
    width: 100%;
    height: 100%
  }

  .movie__detail {
    width: 95%;
    height: 80%;
    margin-top: 50px;
    flex-direction: column;
    padding-left: 30px;
  }

  .movie__detail img {
    width: 150px;
    height: 200px;
    margin-right: 70px;
  }

  .movie__detail .movie__detail__data .movie__title {
    font-size: 20px;
    margin-bottom: 10px;
  }

  .movie__detail .movie__detail__data .movie__year  {
    font-size: 15px;
    padding-bottom: 0;
  }

  .movie__detail .movie__detail__data .movie__genres .movie__genre {
    font-size: 14px;
    position: relative;
    top: -15px;
  }

  .movie__detail .movie__detail__data .movie__summary {
    margin-top: 0;
    font-size: 15px;
  }
}

@media screen and (max-width: 380px) {
  .movie__detail {
    padding-left: 10px;
    margin-top: 20px;
  }

  .movie__detail img {
    width: 150px;
    height: 200px;
    margin-left: -40px;
  }

  .movie__detail .movie__detail__data {
    margin-left: 40px;
  }

  .movie__detail .movie__detail__data .movie__genres {
    flex-direction: column;
  }
}
.nav {
    background-color: aliceblue;
    height: 75px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 20px;
    border-radius: 5px;
    box-shadow: 0 20px 30px -5px rgba(50, 50, 93, 0.25);
    position: fixed;
    left: 0;
    top: 0;
}

.nav .home {
    border-bottom: 1px solid #D4D4D4;
    padding-bottom: 5px;
}

.nav .about {
    padding-top: 5px;
}

a {
    text-decoration: none;
    color:#34B99A;
    margin-bottom: 5px;
}

@media screen and (max-width: 480px) {
    .nav {
        flex-direction: row;
        align-items: center;
        height: 55px;
    }

    .nav .home {
        border-bottom: none;
        border-right: 1px solid #D4D4D4;
        padding-bottom: 0;
        margin-right: 14px;
        padding-right: 14px;
    }

    .nav .about {
        padding-top: 0;
    }

    a {
        margin-bottom: 0;
    }
}
