.movies {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
  padding: 0;
  padding-top: 80px;
  width: 100%;
}

.movies a {
  width: 45%;
}

.movies .movie {
  background-color: var(--white-color);
  margin-bottom: 70px;
  margin-right: 10px;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  padding: 20px;
  border-radius: 6px;
  color: #adaeb9;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.movies .movie img {
  position: relative;
  top: -40px;
  max-width: 120px;
  margin-right: 20px;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.movies .movie .movie__data {
  padding-right: 20px;
  margin-top: 12px;
  display: inline-block;
}

.movies .movie .movie__data .movie__title,
.movies .movie .movie__data .movie__year {
  margin: 0;
}

.movies .movie .movie__data .movie__title {
  margin-bottom: 8px;
  font-weight: 400;
  font-size: 18px;
  color: #2c2c2c;
}

.movies .movie .movie__data .movie__genres {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  margin-top: 5px;
}

.movies .movie .movie__data .movie__genres li {
  margin-right: 8px;
}

.movies .movie .movie__data .movie__summary {
  font-size: 14px;
}

@media screen and (max-width: 1380px) {
  .movies .movie img {
    max-width: 110px;
  }
  .movies .movie .movie__data .movie__title {
    font-size: 18px;
  }

  .movies .movie .movie__data .movie__genres {
    font-size: 14px;
  }

  .movies .movie .movie__data .movie__summary {
    font-size: 13px;
  }
}

@media screen and (max-width: 1200px) {
  .movies {
    width: 95%;
  }
}

@media screen and (max-width: 1180px) {
  .movies {
    width: 100%;
  }

  .movies .movie {
    margin-bottom: 40px;
  }
}

@media screen and (max-width: 1100px) {
  .movies .movie img {
    max-width: 100px;
  }

  .movies .movie .movie__data .movie__title {
    font-size: 16px;
  }

  .movies .movie .movie__data .movie__genres {
    font-size: 13px;
  }
}

/* Tablet */
@media screen and (max-width: 1024px) {
  .movies {
    display: block;
    width: 95%;
    margin-top: 40px;
    margin-left: ;
  }
  .movies .movie {
    margin-bottom: 60px;
  }

  .movies .movie img {
    max-width: 140px;
  }

  .movies .movie .movie__data {
    margin-left: 30px;
  }

  .movies .movie .movie__data .movie__title {
    font-size: 24px;
    margin-bottom: 20px;
  }

  .movies .movie .movie__data .movie__genres {
    font-size: 16px;
  }

  .movies .movie .movie__data .movie__summary {
    font-size: 14px;
  }
}

/* mobile */
@media screen and (max-width: 480px) {
  .movies {
    display: block;
    width: 80%;
  }

  .movies a {
    width: 40%;
  }

  .movies .movie {
    padding: 15px;
    margin-right: 0;
    padding-right: 70px;
  }

  .movies .movie img {
    max-width: 100px;
  }

  .movies .movie .movie__data .movie__title {
    font-size: 15px;
  }
}
